@import "../../common.scss";

.popular_cover {
  width: 100vw;
  max-width: 1200px;
  padding: 20px 0 50px;
  text-align: center;

  > .top4_title {
    font-size: 2.5em;
    padding: 50px 0 30px;
  }
  > .top4_item {
    display: flex;
  }

  .popular {
    width: calc(100% / 4);

    /* TOP4 영화 이미지 박스 */

    .img_cover {
      position: relative;
      width: 250px;
      height: 375px;
      overflow: hidden;

      &:hover > p {
        top: 0;
        z-index: 10;
        border-bottom: 1px solid #333;
        transition: all 0.5s ease-out;
      }
    }

    .movie_summary {
      position: absolute;
      top: -325px;
      padding: 35px 20px;
      height: calc(100% - 50px - 70px);
      word-break: break-all;
      color: #fff;
      background-color: rgba(0, 0, 0, 0.7);
      transition: all 0.5s ease-in;
    }

    .movie_img {
      width: 100%;
      height: 100%;
    }

    .like_ticketing {
      position: absolute;
      display: flex;
      width: 100%;
      height: 50px;
      bottom: 0;
      z-index: 5;
      background-color: rgba(0, 0, 0, 0.7);

      div {
        cursor: pointer;
      }

      > .like_btn {
        width: 50px;
        height: 100%;

        &:hover path {
          stroke: var(--point-color);
          fill: var(--point-color);
        }
        path {
          stroke-width: 10px;
          stroke: #fff;
        }

        .like_heart {
          width: 60%;
        }
      }

      > .ticketing_btn {
        width: calc(100% - 50px);
        color: var(--gray-color);
        line-height: 50px;
        border-left: 1px solid #333;

        &:hover {
          color: #fff;
        }
      }
    }

    /* TOP4 영화 제목 / 징르 */
    .movie_title {
      width: 250px;
      padding: 20px;
      font-size: 1.2em;
      color: var(--gray-color);
      font-weight: 500;
    }

    .genres_cover {
      display: flex;
      margin: 0 auto;

      .genres {
        display: flex;
        flex-wrap: wrap;
        justify-content: left;

        li {
          flex-grow: 1;
          flex-shrink: 1;
          font-size: 0.8em;
          padding: 3px 5px;
          margin: 0 3px 10px;
          border-radius: 5px;
          border: 1px solid var(--gray-color);
        }
      }
    }
  }
}
