@charset "UTF-8";
:root {
  --point-color: #ff0000;
  --gray-color: #666;
  --background-color: #000;
}

.popular_cover {
  width: 100vw;
  max-width: 1200px;
  padding: 20px 0 50px;
  text-align: center;
}

.popular_cover > .top4_title {
  font-size: 2.5em;
  padding: 50px 0 30px;
}

.popular_cover > .top4_item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.popular_cover .popular {
  width: calc(100% / 4);
  /* TOP4 영화 이미지 박스 */
  /* TOP4 영화 제목 / 징르 */
}

.popular_cover .popular .img_cover {
  position: relative;
  width: 250px;
  height: 375px;
  overflow: hidden;
}

.popular_cover .popular .img_cover:hover > p {
  top: 0;
  z-index: 10;
  border-bottom: 1px solid #333;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

.popular_cover .popular .movie_summary {
  position: absolute;
  top: -325px;
  padding: 35px 20px;
  height: calc(100% - 50px - 70px);
  word-break: break-all;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.7);
  -webkit-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
}

.popular_cover .popular .movie_img {
  width: 100%;
  height: 100%;
}

.popular_cover .popular .like_ticketing {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 50px;
  bottom: 0;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0.7);
}

.popular_cover .popular .like_ticketing div {
  cursor: pointer;
}

.popular_cover .popular .like_ticketing > .like_btn {
  width: 50px;
  height: 100%;
}

.popular_cover .popular .like_ticketing > .like_btn:hover path {
  stroke: var(--point-color);
  fill: var(--point-color);
}

.popular_cover .popular .like_ticketing > .like_btn path {
  stroke-width: 10px;
  stroke: #fff;
}

.popular_cover .popular .like_ticketing > .like_btn .like_heart {
  width: 60%;
}

.popular_cover .popular .like_ticketing > .ticketing_btn {
  width: calc(100% - 50px);
  color: var(--gray-color);
  line-height: 50px;
  border-left: 1px solid #333;
}

.popular_cover .popular .like_ticketing > .ticketing_btn:hover {
  color: #fff;
}

.popular_cover .popular .movie_title {
  width: 250px;
  padding: 20px;
  font-size: 1.2em;
  color: var(--gray-color);
  font-weight: 500;
}

.popular_cover .popular .genres_cover {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
}

.popular_cover .popular .genres_cover .genres {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: left;
      -ms-flex-pack: left;
          justify-content: left;
}

.popular_cover .popular .genres_cover .genres li {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  font-size: 0.8em;
  padding: 3px 5px;
  margin: 0 3px 10px;
  border-radius: 5px;
  border: 1px solid var(--gray-color);
}
